/* About Me */
.about-me {
    padding: 100px 30px;
    overflow: hidden;
    h2 {
        font-weight: 600;
        width: 30%;
        @media #{$mediaExSmall} {
            width: 50%
        }
        @media #{$mediaSmall} {
            width: 50%
        }
        @media #{$mediaMedium} {
            width: 40%
        }
        @media #{$mediaLarge} {
            width: 35%
        }
    }
    p {
        font-size: 15px;
        line-height: 25px;
        @media #{$mediaExSmall} {
            font-size: 13px;
            line-height: 20px;
            margin-bottom: 50px;
        }
        @media #{$mediaSmall} {
            font-size: 13px;
            line-height: 20px;
            margin-bottom: 50px;
        }
        @media #{$mediaMedium} {
            font-size: 14px;
            line-height: 23px;
            margin-bottom: 30px;
        }
        @media #{$mediaLarge} {
            font-size: 14px;
            line-height: 23px;
            margin-bottom: 30px;
        }
    }
    .video {
        position: relative;
        iframe {
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 88%;
            height: 70%; 
        }
    }
}

/* Wixcard */
.wixcard {
    padding: 100px 30px;
    background-color: rgba(#333, .1);
    overflow: hidden;
    h2 {
        font-size: 22px;
        font-weight: 600;
        width: 30%;
        @media #{$mediaExSmall} {
            font-size: 17px;
            width: 85%;
        }
        @media #{$mediaSmall} {
            font-size: 19px;
            width: 60%;
        }
        @media #{$mediaMedium} {
            font-size: 20px;
            width: 50%;
        }
        @media #{$mediaLarge} {
            font-size: 20px;
            width: 40%;
        }
    }
    p {
        font-size: 18px;
        line-height: 25px;
        width: 90%;
        margin-bottom: 80px;
        @media #{$mediaExSmall} {
            font-size: 14px;
        }
        @media #{$mediaSmall} {
            font-size: 15px;
        }
        @media #{$mediaMedium} {
            font-size: 16px;
        }
        @media #{$mediaLarge} {
            font-size: 17px;
        }
    }
    h3 {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 30px;
        background-color: #333;
        color: #fff;
        padding: 10px;
        width: 25%;
        text-shadow: 0 0 6px #000;
        text-align: center;
        @media #{$mediaExSmall} {
            font-size: 16px;
            width: 85%
        }
        @media #{$mediaSmall} {
            font-size: 16px;
            width: 60%;
        }
        @media #{$mediaMedium} {
            font-size: 17px;
            width: 50%;
        }
        @media #{$mediaLarge} {
            font-size: 17px;
            width: 40%;
        }
    }
    span {
        display: block;
        text-align: right;
        padding: 3px 10px;
        margin-bottom: 15px;
        padding-right: 20px;
        position: relative;
        font-size: 15px;
        text-shadow: 0 0 .05px #000;
        @media #{$mediaExSmall} {
            font-size: 13px
        }
        @media #{$mediaSmall} {
            font-size: 13px
        }
        @media #{$mediaMedium} {
            font-size: 14px
        }
        @media #{$mediaLarge} {
            font-size: 14px
        }
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            width: 15px;
            height: 1px;
            background: $mainColor
        }
    }
}

/* Section Model Question */
.modal {
    background-color: rgba(#262626, 0.7);
    z-index: 9999;
    padding: 50px 30px;
    text-align: right;
    .modal-dialog {
        max-width: 95%;
        .modal-content {
            background-color: rgba(#000, .4);
            .modal-header {
                border: none;
                h5 {
                    font-size: 18px;
                    font-weight: 500;
                    background: $mainColor;
                    border-right: 5px solid #333;
                    color: #fff;
                    padding: 6px 20px;
                    margin-top: 15px;
                    width: 30%;
                    text-shadow: 0 0 3px rgba(#000, .9);
                    @media #{$mediaExSmall} {
                        font-size: 15px;
                        width: 80%;
                    }
                    @media #{$mediaSmall} {
                        font-size: 15px;
                        width: 80%;
                    }
                    @media #{$mediaMedium} {
                        font-size: 17px;
                        width: 40%;
                    }
                    @media #{$mediaLarge} {
                        font-size: 19px;
                        width: 35%;
                    }
                }
                .close {
                    padding: 0;
                    margin: 15px;
                    color: #fff;
                }
            }
            .modal-body {
                .card {
                    background-color: rgba(#000, .4);
                    color: #fff;
                    .btn-link {
                        color: $mainColor;
                        text-decoration: none;
                        @media #{$mediaExSmall} {
                            font-size: 13px;
                        }
                        @media #{$mediaSmall} {
                            font-size: 13px;
                        }
                        @media #{$mediaMedium} {
                            font-size: 14px;
                        }
                        @media #{$mediaLarge} {
                            font-size: 14px;
                        }
                    }
                    .card-body {
                        font-size: 14px;
                        line-height: 30px;
                        color: #dee2e6;
                        @media #{$mediaExSmall} {
                            font-size: 12px;
                        }
                        @media #{$mediaSmall} {
                            font-size: 12px;
                        }
                        @media #{$mediaMedium} {
                            font-size: 13px;
                        }
                        @media #{$mediaLarge} {
                            font-size: 13px;
                        }
                    }
                }
            }
        }
    }
}

/* Our Clients */
.our-clinets {
    padding: 100px 30px;
    text-align: right;
    h2 {
        margin-bottom: 90px;
    }
    .card {
        border: none;
        text-align: center;
        .card-img-overlay {
            background-color: rgba(#000, .65);
            width: 100%;
            height: 100%;
            opacity: 0;
            top: auto;
            left: auto;
            right: 0%;
            transition: .3s;
            transform: scale(.5);
            cursor: pointer;
            font-size: 30px;
            color: #fff;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            .card-title, .card-text {
                font-family: 'Cairo', sans-serif;
                text-align: center;
                color: $mainColor;
                margin: 0px;
                text-shadow: 0 0 3px #000;
                font-weight: 600
            }
            .card-title {
                font-size: 20px;
                margin-top: 50px;
                margin-bottom: 10px;
                @media #{$mediaExSmall} {
                    font-size: 12px
                }
                @media #{$mediaSmall} {
                    font-size: 12px
                }
                @media #{$mediaMedium} {
                    font-size: 14px
                }
                @media #{$mediaLarge} {
                    font-size: 16px
                }
            }
            .card-text {
                @media #{$mediaExSmall} {
                    font-size: 17px
                }
                @media #{$mediaSmall} {
                    font-size: 17px
                }
                @media #{$mediaMedium} {
                    font-size: 18px
                }
                @media #{$mediaLarge} {
                    font-size: 22px
                }
            }
        }
        &:hover {
            .card-img-overlay {    
                opacity: 1;
                transform: scale(1);
            }
        }
    }
}

/* Contact Us */
.contact-us {
    padding: 100px 30px;
    background-color: rgba(#333, .1);
    text-align: right;
    form {
        .form-control {
            border-radius: 3px;
            &:focus {
                border-color: $mainColor;
                box-shadow: 0 0 0 0.2rem rgba($mainColor ,.25)
            }
        }
        button {
            background-color: #333;
            border-radius: 2px;
            border: 1px solid rgba(#333, .3);
            color: #fff;
            text-shadow: 0 0 1px rgba(#000, .9);
            padding: 5px 30px;
            transition: .2s;
            &:hover {
                background-color: $mainColor;
                color: #ffffff
            }

        }
    }
}

/* Google Map */
.mapouter{
    position:relative;
    text-align:right;
    height:500px;
    width:100%
}
.gmap_canvas {
    overflow:hidden;
    background:none!important;
    height:500px;
    width:100%
}