/* Footer */
.footer {
    padding: 20px;
    background-color: #333;
    text-align: center;
    .navbar-nav {
        position: fixed;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        background-color: rgba(#000, .8);
        z-index: 999;
        .nav-item {
            display: inline-block;
            width: 40px;
            height: 40px;
            line-height: 35px;
            background-color: #333;
            border-radius: 2px;
            margin: 10px;
            font-size: 25px;
            padding: 5px;
            transition: .3s;
            cursor: pointer;
            @media #{$mediaExSmall} {
                font-size: 17px;
                width: 30px;
                height: 30px;
                line-height: 23px;
            }
            @media #{$mediaSmall} {
                font-size: 17px;
                width: 30px;
                height: 30px;
                line-height: 23px;
            }
            @media #{$mediaMedium} {
                font-size: 18px;
                width: 35px;
                height: 35px;
                line-height: 25px;
            }
            @media #{$mediaLarge} {
                font-size: 19px;
                width: 40px;
                height: 40px;
                line-height: 28px;
            }
            &:hover {
                &:nth-child(1) {
                    background-color: #3b5999;
                }
                &:nth-child(2) {
                    background-color: #cd201f;
                }
                &:nth-child(3) {
                    background-color: #e4405f;
                }
                &:nth-child(4) {
                    background-color: #dd4b39;
                }
                &:nth-child(5) {
                    background-color: #25D366;
                }
            }
            a {
                color: #fff;
            }
        }
    }
    .copyright {
        color: #fff;
        text-align: right;
        font-size: 14px;
        @media #{$mediaExSmall} {
            font-size: 12px
        }
        @media #{$mediaSmall} {
            font-size: 12px
        }
        @media #{$mediaMedium} {
            font-size: 13px
        }
        @media #{$mediaLarge} {
            font-size: 13px
        }
    }
    .scroll-top {
        position: fixed;
        bottom: 15px;
        right: 15px;
        width: 40px;
        height: 40px;
        line-height: 40px;
        background-color: $mainColor;
        border-radius: 3px;
        border: 1px solid #333;
        transition: .3s;
        cursor: pointer;
        z-index: 999;
        &:hover {
            padding-bottom: 50px;
        }
    }
}