* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    direction: rtl;
    font-family: 'Tajawal', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Cairo', sans-serif;
    text-align: right
}

p {
    font-family: 'Tajawal', sans-serif;
    text-align: right;
    text-shadow: 0 0 .01px rgba(#000, .5);
}

/* Style H2 */
h2 {
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 40px;
    background: $mainColor;
    border-right: 5px solid #333;
    color: #fff;
    padding: 7px 16px;
    width: 20%;
    text-shadow: 0 0 3px rgba(#000, .9);
    @media #{$mediaExSmall} {
        font-size: 20px;
        width: 50%
    }
    @media #{$mediaSmall} {
        font-size: 20px;
        width: 50%
    }
    @media #{$mediaMedium} {
        font-size: 22px;
        width: 40%
    }
    @media #{$mediaLarge} {
        font-size: 23px;
        width: 35%
    }
}