/* Craousel */
.carousel {
    @media #{$mediaExSmall} {
        height: 100% !important;
    }
    @media #{$mediaSmall} {
        height: 100% !important;
    }
    @media #{$mediaMedium} {
        height: 100% !important;
    }
    @media #{$mediaLarge} {
        height: 100% !important;
    }
    .carousel-inner {
        .carousel-item {
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(#333, .5);
                z-index: 99;
            }
        }
        .carousel-caption {
            position: absolute;
            top: 40%;
            left: 50%;
            right: auto;
            bottom: auto;
            transform: translate(-40%, -50%);
            background-color: rgba(#000, .6);
            padding: 30px;
            border-radius: 5px;
            height: auto;
            width: 700px;
            z-index: 999;
            h4 {
                background-color: $mainColor;
                font-weight: 600;
                padding: 10px;
                width: 60%;
                border-radius: 2px;
                margin-bottom: 15px
            }
            p {
                font-size: 18px;
                line-height: 30px
            }
        }
    }
    .carousel-control-prev, .carousel-control-next {
        z-index: 999;
    }
}